<template>
  <div class="flex h-screen">
    <div class="m-auto">
      <div :class="[ change ? 'grid grid-cols-2 gap-6':'grid']">
        <div>
          <div>
            <LineComponent :val='rootIcon[5]' />
            <LineComponent :val='rootIcon[4]' />
            <LineComponent :val='rootIcon[3]' />
            <LineComponent :val='rootIcon[2]' />
            <LineComponent :val='rootIcon[1]' />
            <LineComponent :val='rootIcon[0]' />
          </div>
          <div class="mt-4 text-center font-bold">{{ rootHex.id }}</div>
        </div>
        <div v-if="change">
          <div>
            <LineComponent :val='swayIcon[5]' />
            <LineComponent :val='swayIcon[4]' />
            <LineComponent :val='swayIcon[3]' />
            <LineComponent :val='swayIcon[2]' />
            <LineComponent :val='swayIcon[1]' />
            <LineComponent :val='swayIcon[0]' />
          </div>
          <div class="mt-4 text-center font-bold">{{ swayHex.id }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import LineComponent from './components/Line'

export default {
  name: 'App',
  components: {
    LineComponent
  },
  data () {
    return {
      iching: '',
      pile: '',
      east: '',
      west: '',
      rootIcon: '',
      swayIcon: '',
      change: true,
      rootHex: '',
      swayHex: ''
    }
  },
  methods: {
    divideStalks (yarrowStalks) {
      var westPile = Math.floor((Math.random() * yarrowStalks) + 1)
      var eastPile = yarrowStalks - westPile
      westPile = westPile - 1
      
      this.east = (eastPile % 4)
      this.west = (westPile % 4)

      if (this.east === 0) { this.east = 4 }
      if (this.west === 0) { this.west = 4 }

      this.pile = this.east + this.west + 1
    },
    lineCast () {
      var stalks = 49

      var countValueOne = 0
      var countValueTwo = 0
      var countValueThree = 0

      this.divideStalks(stalks)

      if (this.east + this.west + 1 === 9) { countValueOne = 2 }
      if (this.east + this.west + 1 === 5) { countValueOne = 3 }
      
      stalks = stalks - this.pile

      this.divideStalks(stalks)

      if (this.east + this.west + 1 === 8) { countValueTwo = 2 }
      if (this.east + this.west + 1 === 4) { countValueTwo = 3 }
      
      stalks = stalks - this.pile

      this.divideStalks(stalks)

      if (this.east + this.west + 1 === 8) { countValueThree = 2 }
      if (this.east + this.west + 1 === 4) { countValueThree = 3 }
      
      var lineValue = countValueOne + countValueTwo + countValueThree

      return lineValue
    },
    getAnswer () {
      let arr = []
      for (let i = 0; i < 6; i++) { arr.push(this.lineCast()) }
      
      let iconOpts = { 6: 7, 7: 7, 8: 8, 9: 8 }
      this.rootIcon = arr.join('')
      this.swayIcon = this.rootIcon.replace(/[6789]/g, m => iconOpts[m])

      if (this.rootIcon === this.swayIcon) this.change = false

      let rootOpts = { 6: 0, 7: 1, 8: 0, 9: 1 }
      this.rootHex = this.rootIcon.replace(/[6789]/g, m => rootOpts[m])
      this.rootHex = this.iching.find(hex => hex.code === this.rootHex.toString())

      let swayOpts = { 6: 0, 7: 1, 8: 0, 9: 1 }
      this.swayHex = this.swayIcon.replace(/[6789]/g, m => swayOpts[m])
      this.swayHex = this.iching.find(hex => hex.code === this.swayHex.toString())
    }
  },
  mounted () {
    axios.get('/js/chunk-data.js')
      .then(response => {
        this.iching = response.data
        this.getAnswer()
      })
      .catch(error => console.log(error))
  }  
}
</script>

<style src="./assets/css/styles.css"></style>
